import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Currency } from '../common/currency';
import { Observable, throwError } from 'rxjs';
import { catchError, retry, map } from 'rxjs/operators';
import { CurrencyConvertion } from '../common/currency-convertion';
import { CurrencyRate } from '../common/currency-rate';
import { Date } from '../common/date';

@Injectable({
  providedIn: 'root'
})

export class CurrencyService {

  private url: string = "http://localhost:5000/api/currencies/v1";

  constructor(private http: HttpClient) { }

  getCurrencyList(): Observable<Currency[]> {
    return this.http.get<Currency[]>(this.url);
  }

  getCurrencyRate(currencyConvertion: CurrencyConvertion): Observable<CurrencyRate> {
      return this.http.post<CurrencyRate>(this.url, currencyConvertion);
  }

  getDate(): Observable<Date> {
    return this.http.get<Date>(this.url+"/today");
  }

}
