import { Component, OnInit, SystemJsNgModuleLoader } from '@angular/core';
import { CurrencyService } from '../services/currency-service.service';
import { Currency } from '../common/currency';
import { ConsoleService } from '@ng-select/ng-select/lib/console.service';
import { CurrencyConvertion } from '../common/currency-convertion';
import { CurrencyRate } from '../common/currency-rate';
import { min } from 'rxjs/operators';
import { Date } from '../common/date';

@Component({
  selector: 'app-currency-exchanger',
  templateUrl: './currency-exchanger.component.html',
  styleUrls: ['./currency-exchanger.component.css']
})
export class CurrencyExchangerComponent implements OnInit {

  currencies: Currency[];
  private from: number;
  private to: number;
  public result: string;
  public input: number;
  public rate: CurrencyRate;
  public today: Date;

  constructor(private currencyService: CurrencyService) { }

  ngOnInit(): void {
    this.result = 0.00000 + "";
    this.listCurrencies();
    this.getToday();
  }

  onChangeFrom = ($event: any): void => {
    if($event != null) {
      this.from = $event.id;
      this.checkSafeToGetRates();
    } else {
      this.result = 0.00000 + "";
    }
  }

  onChangeTo = ($event: any): void => {
    if($event != null) {
      this.to = $event.id;
      this.checkSafeToGetRates();
    } else {
      this.result = 0.00000 + "";
    }
  }

  onChangeToCurrency = (toInput: number): void => {
    if( (this.isFromAndToWithData()) && (toInput != null) && (this.rate.currency_value != null)) {
      this.input = toInput;
      this.result = (this.input * this.rate.currency_value).toPrecision(6);
    } else {
      this.result = 0.00000 + "";
    }
  }

  async mirtiesKelias(values: CurrencyConvertion) {
    this.currencyService.getCurrencyRate(values).subscribe (
      data => {
        this.rate = data;
      }
    );
  }

  async getToday() {
    this.currencyService.getDate().subscribe (
      data => {
        this.today = data;
      }
    )
  }

  listCurrencies() {
    this.currencyService.getCurrencyList().subscribe (
      data => {
        this.currencies = data;
      }
    )
  }

  calculateCurrencyResult(): void {
      if( (this.input == null) || (this.rate.currency_value == null)) {
        this.result = "0.00000";
      }
      
      this.result = (this.rate.currency_value * this.input).toFixed(5);
  }

  isFromAndToWithData(): boolean {
    if ((this.from != null) && (this.to != null)) {
      return true;
    }
    return false;
  }

  checkSafeToGetRates(): void {
    if ((this.from != null) && (this.to != null)) {
        let values: CurrencyConvertion = new CurrencyConvertion(this.from, this.to);
        console.log("BUVO SAUGU ATNAUJINTI DUOMENIS");
        console.log(values)
        this.mirtiesKelias(values);
        console.log(this.rate);
        this.calculateCurrencyResult();
    }
  }

  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;

  }

}
